.post {
  padding-bottom: 90px;

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    margin: 0;
    margin-bottom: 14px;
  }

  &__content {
    position: relative;
    padding-top: 60px;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: -60px;
    margin-left: -15px;
    margin-right: -15px;
  }

  &__pagination {
    text-align: center;
    margin-top: 50px;

    & .swiper-pagination-bullet {
      border: 2px solid #039583;
      border-radius: 2px;
      width: 22px;
      height: 22px;
      background-color: transparent;
    }

    & .swiper-pagination-bullet-active {
      border: 2px solid #039583;
      border-radius: 2px;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 14px;
        height: 14px;
        background-color: #039583;
        transform: translate(-50%, -50%);
      }
    }
  }
}

.post-card {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);

  &__item {
    grid-column: 3 span;
  }

  &__date {
    display: block;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--date-color);
    margin-top: 10px;
    margin-bottom: 25px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 12px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 30px;
  }

  &__link {
    font-size: 20px;
    line-height: 24px;
    color: var(--bg-nav-color);
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: -63px;
      top: 9px;
      width: 41px;
      height: 9px;
      background-image: url(../img/arr-card.svg);
    }
  }
}
