.header {
  padding-top: 10px;

  &__top {
    display: flex;
    justify-content: space-between;
  }
}

.logo {}

.header-contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: translate(11.5%, -12px);

  &__icon {
    width: 30px;
    height: 30px;
    fill: var(--decor-color);
    margin-right: 15px;
  }

  &__address {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 19px;
    color: var(--primary-color);
  }

  &__mail {
    display: flex;
    align-items: center;
    margin-left: 196px;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--primary-color);

  }

  &__tel {
    display: flex;
    align-items: center;
    margin-left: 200px;
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    font-family: "BebasNeue", sans-serif;
    color: var(--primary-color);
  }
}

.lk {
  display: flex;
  align-items: center;
  transform: translateY(-10px);

  &__icon {
    &-login {
      width: 39px;
      height: 39px;
      fill: #00504B;
    }

    &-cart {
      width: 49px;
      height: 47px;
      fill: #00504B;
    }
  }

  &__login {
    padding: 16px;
    position: relative;
    border-left: 1px solid rgba(15, 193, 175, 0.2);
    border-right: 1px solid rgba(15, 193, 175, 0.2);
  }

  &__cart {
    margin-left: 13px;
  }
}

.nav {
  background-color: var(--bg-nav-color);
  padding-top: 15px;
  padding-bottom: 15px;
  position: absolute;
  top: 72px;
  left: 0;
  width: 100%;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list {
    display: inline-flex;
    justify-content: flex-start;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 61px;
    }

    &:first-child {
      border-right: 1px solid rgba(15, 193, 175, 0.2);
      padding-top: 22px;
      padding-bottom: 22px;
      padding-right: 53px;
      margin-top: -22px;
      margin-bottom: -22px;
      margin-right: 34px;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: 30px;
        width: 8px;
        height: 10px;
        background-image: url(../img/arr.svg);
        transform: translateY(-50%);
      }
    }
  }

  &__link {
    font-weight: 800;
    font-size: 16px;
    line-height: 16px;
    color: var(--light-color);
  }
}

.header__search {
  width: 27px;
  height: 27px;
  fill: #0FC1AF;
}
