.balloon {
  padding: 15px 15px 35px 12px;

  &__title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    line-height: 38px;
    color: #039583;
    margin: 0;
    margin-bottom: 25px;
  }

  &__list {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    font-style: normal;
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    padding-left: 53px;
    background-position: left center;
    background-size: 40px 40px;
    background-repeat: no-repeat;
  }

  &__item-tel {
    margin-bottom: 20px;
    background-position: left center;
    background-size: 23px 23px;
    background-repeat: no-repeat;
    background-image: url(../img/bal-phone.svg);
  }

  &__item-mail {
    margin-bottom: 20px;
    background-position: left center;
    background-size: 23px 23px;
    background-repeat: no-repeat;
    background-image: url(../img/bal-mail.svg);
  }

  &__item-address {
    background-position: left center;
    background-size: 23px 23px;
    background-repeat: no-repeat;
    background-image: url(../img/bal-map.svg);
    padding-left: 53px;

  }

  &__item-street {
    font-style: normal;
    margin-bottom: 20px;
  }

  &__item-time {
    margin-bottom: 20px;
    background-position: left center;
    background-size: 23px 23px;
    background-repeat: no-repeat;
    background-image: url(../img/bal-clock.svg);
    padding-left: 53px;
  }

  &__item-time-text {
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

[class*="balloon__close-button"] {
  padding-right: 20px;
  padding-top: 20px;
}
