.footer {
  background-color: var(--bg-color);
  padding-top: 17px;
  padding-bottom: 10px;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap);
    margin-bottom: 17px;
  }

  &__logo {
    grid-column: 3 span;
  }

  &__right {
    grid-column: 9 span;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: var(--grid-gap);
  }

  &__nav {
    grid-column: 2 span;
  }

  &__nav-title {
    font-weight: 800;
    font-size: 16px;
    line-height: 16px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 7px;
  }

  &__nav-link {
    background: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    width: 50px;
    height: 30px;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: bottom;

    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #FAFEFD;
  }

  &__nav-img {
    margin-bottom: 25px;
  }

  &__nav--social {

    & .footer__nav-link {
      width: 30px;
      height: 30px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__artix {
    fill: #FAFEFD;
    width: 20px;
    height: 21px;
    margin-left: 13px;
  }
}

