.cards {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);

  &__item {
    grid-column: 3 span;
    background: var(--light-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding: 40px 26px 33px 24px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__link {
    font-size: 20px;
    line-height: 24px;
    color: var(--bg-nav-color);
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: -63px;
      top: 8px;
      width: 41px;
      height: 9px;
      background-image: url(../img/arr-card.svg);
    }
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 38px;
  }

  &__item-list {}

  &__item-item {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    padding-left: 40px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 10px;
      width: 9px;
      height: 9px;
      background: #FF9903;
      transform: rotate(-45deg) translateY(-50%);
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &:last-child {
      margin-bottom: 40px;
    }
  }

  &__image {
    margin-bottom: 35px;
  }

  &__wrapper {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 39px;
  }

  &__desc {


    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: var(--primary-color);

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__price {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 48px;
    line-height: 48px;
    text-align: right;
    color: var(--primary-color);

    &--text {
      font-size: 25px;
    }
  }


  &__btn {
    background-color: #039583;
    border-radius: 2px;
    display: inline-block;
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 28px;
    line-height: 38px;
    color: var(--light-color);
    padding: 14px 0;
    width: 100%;
  }
}
